.hi-alert{position:relative;padding:12px;background:#ebf2fd;border:1px solid #b3cdfb;border-radius:2px;color:#4284f5;font-size:14px;overflow:hidden;min-width:280px;display:block}.hi-alert .icon-contain{position:absolute;top:50%;left:16px;-webkit-transform:translateY(-50%);transform:translateY(-50%);font-size:0;color:#4284f5}.hi-alert .icon-contain .hi-icon{font-size:20px}.hi-alert .text-contain{margin:0 32px;font-weight:400}.hi-alert .text-contain .text-title{font-size:14px;margin-bottom:4px}.hi-alert .text-contain .text-message{font-size:12px}.hi-alert .close-btn{position:absolute;top:50%;right:16px;-webkit-transform:translateY(-50%);transform:translateY(-50%);font-size:0;cursor:pointer}.hi-alert .close-btn .hi-icon{font-size:14px;color:#999}.hi-alert.error{background:#fceded;border:1px solid #f7b9b9;color:#eb5252}.hi-alert.error .icon-contain{color:#eb5252}.hi-alert.success{background:#e8f6ee;border:1px solid #a4dbba;color:#1da653}.hi-alert.success .icon-contain{color:#1da653}.hi-alert.warning{background:#fbf5e6;border:1px solid #f3d79d;color:#e19d0c}.hi-alert.warning .icon-contain{color:#e19d0c}.hi-alert.noTitle .text-contain .text-message{font-size:14px}
