.hi-menu ul{margin-top:0;margin-bottom:0;padding-left:0}.hi-menu li+li{margin-top:0}.hi-menu li{list-style:none}.hi-menu.hi-menu-vertical{background:#fff;border:1px solid #e6e7e8;-webkit-box-shadow:0 2px 8px 0 rgba(56,62,71,0.1);box-shadow:0 2px 8px 0 rgba(56,62,71,0.1);border-radius:2px;padding:4px 0}.hi-menu.hi-menu-vertical li{font-size:14px;color:#333;line-height:36px;position:relative;height:36px;cursor:pointer;padding-left:15px}.hi-menu.hi-menu-vertical li:hover{color:#4284f5}.hi-menu.hi-menu-horizontal{border:0;border-bottom:1px solid #e8e8e8;height:48px}.hi-menu.hi-menu-horizontal li{font-size:14px;color:#333;display:inline-block;line-height:20px;padding:14px 4px;margin-right:40px;position:relative;cursor:pointer;margin-top:-1px}.hi-menu.hi-menu-horizontal li:hover{border-bottom:2px solid #4284f5;color:#4284f5}
